import React,{Component} from 'react';
import { Navbar, NavbarBrand,Nav,NavbarToggler,NavItem,Collapse, Jumbotron } from 'reactstrap';
import {NavLink} from 'react-router-dom';
class Header extends Component{
    constructor(props){
        super(props);
        this.state={
            isNavOpen: false
        };
        this.toggleNav = this.toggleNav.bind(this);
    }

    toggleNav(){
        this.setState({
            isNavOpen : !this.state.isNavOpen
        });
    }
    render() {

        return(
            <div>
                <React.Fragment>
                    <Navbar dark expand="md" className="bg-dark">
                        <div className ="container">
                            <NavbarToggler onClick={this.toggleNav} />
                            <NavbarBrand className="mr-auto" href="/">
                            <img src="assets/images/logo.jpg" height = "50" width="50"
                            alt="Flex Toe"/>
                            </NavbarBrand>
                            <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar>
                                <NavItem>
                                    <NavLink className="nav-link" to="/home"><span className="fa fa-home fa-lg"></span> Home </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/featureditems"><span className="fa fa-shopping-cart fa-lg"></span> Shop Now </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/aboutus"><span className="fa fa-sign-in fa-lg"></span> Signup/Login </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/contactus"><span className="fa fa-address-card fa-lg"></span> Contact Us </NavLink>
                                </NavItem>
                            </Nav>
                            </Collapse>
                        </div>
                    </Navbar>
                    <Jumbotron>
                        <div className="container">
                            <div className="row row-header">
                                <div className="col-12 col-sm-6">
                                    <h1>Flex Toe</h1>
                                    <p1>Foot wear with Style</p1>
                                </div>
                            </div>
                        </div>
                    </Jumbotron>
                </React.Fragment>
            </div>
        );
    }
}

export default Header;